import * as React from "react";
import { Link } from "gatsby";
import {
  CloudArrowUpIcon,
  LockClosedIcon,
  ServerIcon,
} from "@heroicons/react/20/solid";
import Layout from "../components/layout";
import CalebPhoto from "../images/people/caleb_photo.png";
import EllisPhoto from "../images/people/ellis_photo.png";
import HarleyPhoto from "../images/people/harley_photo.png";

const people = [
  {
    name: "Caleb Keltner",
    role: "Co-Founder",
    bio:
      "Caleb is an accomplished federal consultant specializing " +
      "in data-driven solutions and training. Holding a Bachelor of " +
      "Science in Economics, Caleb excels in optimizing customer " +
      "acquisition, satisfaction, and retention strategies.",
      imagePath: CalebPhoto,
  },
  {
    name: "Ellis Baughn",
    role: "Co-Founder",
    bio:
      "Ellis has a skilled labor background and a unique understanding " +
      "of issues regarding residential and commercial drainage issues. " +
      "Ellis received a bachelorâ€™s in Civil Engineering and is pursuing " +
      "licensure in the state of Kansas for Water Resource & Environmental " +
      "Engineering. Understanding client concerns and fostering relationships " +
      "with past, current, and future clients are of high importance to Ellis.",
      imagePath: EllisPhoto,
  },
  {
    name: "Harley Stewart",
    role: "Crew Lead",
    bio:
      "Harley has 5 years of professional experience working onsite as a " +
      "landscaping/hardscaping specialist. Experience that has been fostered " +
      "through years of work and different scenarios have molded Harley into a " +
      "technical expert. As a highly skilled laborer he provides qualified and " +
      "competent supervision of all ongoing fieldwork. ",
    imagePath: HarleyPhoto,
  },
];

const OurTeamPage = () => {
  return (
    <Layout>
      <div className="bg-white py-24 sm:py-32">
        <div className="mx-auto grid max-w-7xl gap-x-8 gap-y-20 px-6 lg:px-8 xl:grid-cols-3">
          <div className="max-w-2xl">
            <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
              Meet the team
            </h2>
            <p className="mt-6 text-lg leading-8 text-gray-600">
              Verdant Valleys--a great company built by great people.
            </p>
          </div>
          <ul
            role="list"
            className="grid gap-x-8 gap-y-12 sm:grid-cols-1 sm:gap-y-16 xl:col-span-2"
          >
            {people.map((person) => (
              <li key={person.name}>
                <div className="flex items-center gap-x-6">
                  <img
                    className="h-16 w-16 rounded-full"
                    src={person.imagePath}
                    alt=""
                  />
                  <div>
                    <h3 className="text-base font-semibold leading-7 tracking-tight text-gray-900">
                      {person.name}
                    </h3>
                    <p className="text-sm font-semibold leading-6 text-indigo-600">
                      {person.role}
                    </p>
                    {/* <p className="text-sm font-semibold leading-6 text-gray-600">
                      {person.bio}
                    </p> */}
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </div>
      </div>
      <div className="relative isolate overflow-hidden bg-white px-6 py-24 sm:py-32 lg:overflow-visible lg:px-0">
        <div className="absolute inset-0 -z-10 overflow-hidden">
          <svg
            className="absolute left-[max(50%,25rem)] top-0 h-[64rem] w-[128rem] -translate-x-1/2 stroke-gray-200 [mask-image:radial-gradient(64rem_64rem_at_top,white,transparent)]"
            aria-hidden="true"
          >
            <defs>
              <pattern
                id="e813992c-7d03-4cc4-a2bd-151760b470a0"
                width={200}
                height={200}
                x="50%"
                y={-1}
                patternUnits="userSpaceOnUse"
              >
                <path d="M100 200V.5M.5 .5H200" fill="none" />
              </pattern>
            </defs>
            <svg x="50%" y={-1} className="overflow-visible fill-gray-50">
              <path
                d="M-100.5 0h201v201h-201Z M699.5 0h201v201h-201Z M499.5 400h201v201h-201Z M-300.5 600h201v201h-201Z"
                strokeWidth={0}
              />
            </svg>
            <rect
              width="100%"
              height="100%"
              strokeWidth={0}
              fill="url(#e813992c-7d03-4cc4-a2bd-151760b470a0)"
            />
          </svg>
        </div>
      </div>
    </Layout>
  );
};

export const Head = () => (
  <>
    <title>Our Team</title>
    <meta name="description" content="Description content" />
  </>
);

export default OurTeamPage;
